#rankings-table .country {
  text-align: right;
  /* IE hack: put this inside a span or other line-level element..*/

  width: 140px;
  max-width: 140px;
}
#rankings-table .country a {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #000;
}
#rankings-table .score {
  width: 30px;
  max-width: 30px;
  text-align: right;
  padding-right: 10px;
}
#rankings-table .flag {
  width: 40px;
  max-width: 40px;
  text-align: center;
}
#rankings-table .bar {
  padding: 0 0 0 0;
}
#rankings-table th {
  text-align: center;
  padding: 0;
}
#rankings-table th .sortbyname {
  display: block;
  padding: 2px;
  background: #eee;
  border: 1px solid #ccc;
  color: #000;
}
#rankings-table th .sortbyname .caret {
  margin-top: 9px;
  opacity: 1.0;
}
#rankings-table th .sortbyname.active {
  color: #777;
}
#rankings-table th .sortbyname.active .text {
  text-decoration: underline;
}
#rankings-table th .sortbyname.active .caret {
  opacity: 0.4;
}
#rankings-table th .sortbyname:hover {
  text-decoration: none;
}
#rankings-table th .sortbyname:hover .text {
  text-decoration: underline;
}
