.public-participation h2 {
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
  font-size: 36px;
  margin: 0;
  padding: 5px 0 20px 10px;
}
.public-participation .country-nav-select-outer {
  margin-left: 0;
}
.public-participation .country-nav-select-outer h4 {
  display: inline-block;
  margin-right: 10px;
}
.public-participation .country-nav-select-outer .participation-links {
  display: inline-block;
  float: right;
  text-align: right;
}
#participation-table {
  width: 100%;
}
#participation-table .country {
  text-align: right;
  /* IE hack: put this inside a span or other line-level element..*/

  width: 140px;
  max-width: 140px;
}
#participation-table .country a {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #000;
}
#participation-table .flag {
  width: 40px;
  max-width: 40px;
  text-align: center;
}
#participation-table th {
  text-align: center;
  padding: 4px 0 4px 0;
  background: #eee;
  border: 1px solid #ccc;
}
#participation-table th .sortbyname {
  display: block;
  color: #000;
}
#participation-table th .sortbyname .caret {
  margin-top: 9px;
  opacity: 1.0;
}
#participation-table th .sortbyname.active {
  color: #777;
}
#participation-table th .sortbyname.active .text {
  text-decoration: underline;
}
#participation-table th .sortbyname.active .caret {
  opacity: 0.4;
}
#participation-table th .sortbyname:hover {
  text-decoration: none;
}
#participation-table th .sortbyname:hover .text {
  text-decoration: underline;
}
#participation-table td.letter {
  text-align: center;
  padding: 4px 5px 4px 5px;
}
#participation-table td.letter img {
  cursor: pointer;
}
#participation-table td.letter.inactive {
  opacity: 0.3;
}
#participation-table td.letter.inactive:hover {
  opacity: 0.7;
}
#participation-table td.letter.active {
  background: #eee;
}
#participation-table td.letter.selected.active {
  background: #eee;
}
#participation-table td.letter.selected.inactive {
  background: #dceddc;
  opacity: 1;
}
#participation-table td.letter.selected.inactive:hover {
  opacity: 1;
}
#participation-table td.country,
#participation-table td.flag {
  height: 34px;
}
#participation-table td.score {
  text-align: center;
}
#participation-table tr:hover {
  background: rgba(238, 238, 238, 0.4);
}
#participation-table .table-header:hover {
  background: none;
}
#participation-table .comments-box {
  padding: 0px;
}
#participation-table .comments-box .comments-template {
  padding: 0;
  border-top: 6px solid #eee;
}
#participation-table .comments-box .comments-template .question,
#participation-table .comments-box .comments-template .comments {
  display: inline-block;
  width: 486px;
  vertical-align: top;
  background: #eee;
  padding: 5px 10px 10px 10px;
  margin-bottom: 5px;
}
#participation-table .comments-box .comments-template .question .question-text,
#participation-table .comments-box .comments-template .comments .question-text,
#participation-table .comments-box .comments-template .question .comments-text,
#participation-table .comments-box .comments-template .comments .comments-text {
  background: #ddd;
  padding: 8px;
  margin-bottom: 8px;
}
#participation-table .comments-box .comments-template .question .answer,
#participation-table .comments-box .comments-template .comments .answer {
  margin-top: 5px;
}
#participation-table .comments-box .comments-template .question p,
#participation-table .comments-box .comments-template .comments p {
  white-space: pre;
  white-space: pre-wrap;
  white-space: pre-line;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  white-space: -moz-pre-wrap;
  word-wrap: break-word;
}
#participation-table .comments-box:hover {
  background: none;
}
