#custom-csv {
  width: 400px;
  height: 200px;
  font-size: 8px;
  line-height: 10px;
  font-family: monospace;
}
#custom-csv:hover {
  cursor: pointer !important;
}
#downloadify {
  font-size: 11px;
  line-height: 13px;
  text-align: center;
  font-style: italic;
  width: 400px;
  margin-top: 10px;
  margin-bottom: 2px;
  padding-bottom: 0;
}
.download-data1 {
  width: 440px;
  display: inline-block;
  vertical-align: top;
  padding-left: 10px;
}
.download-data2 {
  width: 440px;
  margin-left: 20px;
  padding-left: 20px;
  display: inline-block;
  vertical-align: top;
}
div.dl-nav-select-outer {
  margin: 10px 0 0 10px;
}
div.dl-nav-select-outer h4 {
  vertical-align: middle;
}
div.dl-nav-select-outer h3 {
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
}
#dl-mode table {
  border-top: 0;
}
#dl-mode table .first-row th {
  border-top: 1px solid #ddd;
}
#dl-mode table .first-row th.blank {
  border: none;
}
#dl-mode table th.blank {
  border: none;
}
#dl-mode table.comp {
  border: none;
}
#dl-mode table.comp td,
#dl-mode table.comp th {
  border: none;
  vertical-align: middle;
}
.dl-years {
  text-align: center;
}
.dl-years .dl-years-info {
  padding-right: 10px;
  vertical-align: bottom;
}
