@font-face {
  font-family: 'clear_sansregular';
  src: url("../fonts/ClearSans-Regular-webfont.eot");
  src: url("../fonts/ClearSans-Regular-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/ClearSans-Regular-webfont.woff2") format("woff2"), url("../fonts/ClearSans-Regular-webfont.woff") format("woff"), url("../fonts/ClearSans-Regular-webfont.ttf") format("truetype"), url("../fonts/ClearSans-Regular-webfont.svg#clear_sansregular") format("svg");
  font-weight: normal;
  font-style: normal;
}
body {
  padding-top: 0;
}
body div {
  font-family: "clear_sansregular", Arial, sans-serif;
  line-height: 1.4;
}
body h2 {
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
  font-size: 36px;
  margin: 0;
  padding: 5px 0 20px 10px;
}
body .container {
  width: 1048px;
}
body .site-main {
  background: url(../images/bg_texture.jpg);
  padding: 0;
  width: 100%;
}
body hr {
  border: none;
  height: 15px;
  margin: 0;
}
body hr.top {
  background: transparent url(../images/gradient-top.png) 50% 0 no-repeat;
}
body a:hover {
  color: #ff7a22;
  text-decoration: none;
}
div#header {
  padding-top: 2px;
  padding-left: 8px;
  padding-bottom: 6px;
  height: 94px;
}
div#header h1#logo1 {
  margin-top: 26px;
  float: left;
}
div#header h1#logo2 {
  margin: 0;
  margin-top: 14px;
  float: right;
}
ul#utility-nav {
  float: right;
  font-size: 11px;
  margin-top: 10px;
  padding-right: 15px;
  width: auto;
}
ul#utility-nav li {
  display: inline;
  border-left: 1px solid #88b8d5;
  padding-left: 5px;
}
ul#utility-nav li:first-child {
  border: none;
}
ul#utility-nav li a {
  color: #88b8d5;
  text-decoration: none;
}
ul#utility-nav li a:hover {
  text-decoration: underline;
}
#main-nav {
  height: 72px;
}
#main-nav .container {
  margin-top: 10px;
}
#main-nav ul {
  list-style-type: none;
  margin: 0;
}
#main-nav ul li {
  display: table;
  float: left;
  position: relative;
  height: 52px;
  width: 10.9%;
  border-left: 2px solid #e3e9f1;
}
#main-nav ul li:first-child {
  border: none;
}
#main-nav ul li a {
  display: table-cell;
  vertical-align: middle;
  height: 42px;
  padding: 5px 10px;
  margin: 0;
  font-size: 15px;
  color: #0087d1;
  font-weight: bold;
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
  text-align: center;
}
#main-nav ul li a:hover {
  color: #fff;
  text-decoration: none;
  background: #0087d1;
}
#main-nav ul li.active a {
  background: #e3e9f1;
}
#main-nav ul li.active a:hover {
  color: #fff;
  text-decoration: none;
  background: #0087d1;
}
#footer-container {
  background: #1a83bd;
  padding-top: 20px;
}
#footer-container #footer {
  display: block;
  padding: 10px 32px;
  width: 984px;
  margin: 0 auto;
}
#footer-container #footer #footer-first,
#footer-container #footer #footer-second {
  float: left;
  padding-right: 10px;
  width: 276px;
}
#footer-container #footer #footer-first .widget-title,
#footer-container #footer #footer-second .widget-title {
  color: #fff;
  font-size: 15px;
  text-transform: uppercase;
  font-family: "Roboto Condensed", Arial, sans-serif;
}
#footer-container #footer #footer-first .textwidget p,
#footer-container #footer #footer-second .textwidget p {
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  margin-bottom: 21px;
}
#footer-container #footer #footer-first .textwidget p .footer-button,
#footer-container #footer #footer-second .textwidget p .footer-button {
  color: #fff;
  margin-right: 10px;
  text-decoration: none;
  background: #3bb3f7 url(../images/arrow_btn_white.png) top right no-repeat;
  border: none;
  cursor: pointer;
  height: 22px;
  line-height: 1;
  padding: 1px 30px 1px 10px;
  text-transform: uppercase;
}
#footer-container #footer #footer-first .textwidget p .footer-button:hover,
#footer-container #footer #footer-second .textwidget p .footer-button:hover {
  color: #fff;
  background: #ff7a22 url(../images/arrow_btn_hov.png) top right no-repeat;
  text-decoration: none;
}
#footer-container .container {
  padding: 10px 32px;
  width: 984px;
}
#footer-container #footer:after,
#footer-container #footer:before {
  content: "";
  display: table;
}
#footer-container #footer:after {
  clear: both;
}
#footer-container ul {
  margin: 0 0 0 0;
  list-style-type: none;
  font-size: 12px;
  padding-top: 10px;
}
#footer-container ul li {
  border-left: 1px solid #fff;
  float: left;
  padding: 0 11px;
}
#footer-container ul li a {
  color: rgba(255, 255, 255, 0.7);
  text-transform: uppercase;
}
#footer-container ul li:first-child {
  border: none;
  padding-left: 0;
}
#footer-container ul li a:hover {
  color: #fff;
  text-decoration: underline;
}
#footer-container ul#beta {
  float: right;
}
#footer-container #copyright {
  float: left;
  padding: 5px 0px 18px 0px;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
}
/* ------------------ */
#content {
  margin: 0 auto;
  width: 1048px;
  min-height: 300px;
  padding: 45px 0px;
}
#content #explorer {
  background-color: #fff;
  padding: 15px;
}
.year-selector {
  margin: 0 0 10px 0;
}
.year-selector button {
  width: 90px;
}
.year-selector .btn.active,
.year-selector .btn:active {
  font-weight: bold;
  font-size: 17px;
}
.percentbar {
  width: 720px;
  height: 20px;
  position: relative;
  background: #aaa;
}
.percentbar > * {
  position: absolute;
  top: 0;
  height: 20px;
  float: left;
}
.percentbar.disabled {
  background: #aaa;
  text-align: center;
  padding-top: 4px;
  height: 16px;
  font-size: 10px;
  color: #fff;
}
.bg-a {
  background: #22aa33;
}
.bg-b {
  background: #DAC402;
}
.bg-c {
  background: #F58024;
}
.bg-d {
  background: #B7282E;
}
.bg-e {
  background: #aaa;
}
.tooltip {
  line-height: 14px;
}
.question-tooltip {
  padding: 10px 4px;
  text-align: left;
  margin: 0 0 0 0;
  font-size: 12px;
}
.tooltip.in {
  opacity: 1.0 !important;
}
.percentbar-tooltip {
  margin-bottom: 4px;
  width: 100px;
}
.percentbar-tooltip th {
  font-weight: normal;
  text-align: center;
  padding: 4px 0;
}
.percentbar-tooltip td.letter {
  width: 40px;
}
.percentbar-tooltip .row-a td {
  background: #197f26;
}
.percentbar-tooltip .row-b td {
  background: #DAC402;
}
.percentbar-tooltip .row-c td {
  background: #f58023;
}
.percentbar-tooltip .row-d td {
  background: #B7282E;
}
.percentbar-tooltip .row-e td {
  background: #888888;
}
.participation-tooltip {
  text-align: left;
  font-size: 14px;
  padding: 4px;
  line-height: 16px;
  margin: 4px;
  color: #f0f0f0;
}
.participation-tooltip th {
  padding: 4px 0;
  text-align: center;
}
.participation-tooltip td {
  padding: 4px 0;
}
.question-template .question-text {
  margin-bottom: 8px;
}
.question-template .answer {
  margin-top: 5px;
}
a#okfn img {
  height: 32px;
  margin-left: 5px;
}
