#content .clearfix {
  background-color: #fff;
}
.timeline-header {
  height: 50px;
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid #000;
  margin-bottom: 8px;
  padding: 20px 0;
}
.timeline-header h2 {
  position: absolute;
  left: 30px;
  top: 0px;
  margin-bottom: 0;
  padding-bottom: 0;
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
  font-size: 36px;
}
.timeline-header h4 {
  position: absolute;
  left: 240px;
  top: 14px;
  margin-bottom: 0;
  padding-bottom: 0;
}
.timeline-header .timeline-toggle {
  position: absolute;
  left: 300px;
  top: 24px;
}
.timeline-header .timeline-toggle label {
  margin-bottom: 0;
}
.timeline-column {
  float: left;
  padding: 0 35px 30px 35px;
}
.timeline-column#timeline-column-2006 {
  padding-left: 3px;
}
.timeline-column#timeline-column-2015 {
  padding-right: 0;
}
.timeline-column h4 {
  text-align: center;
  margin-bottom: 9px;
}
.timeline-column table {
  /* Column widths: */

  background: #f0f0f0;
  font-size: 12px;
  margin-top: 0;
}
.timeline-column table tr:nth-child(even) td {
  background: #f7f7f7;
}
.timeline-column table tr th {
  background: #555;
  color: #fff;
  padding: 6px 5px;
}
.timeline-column table tr td {
  padding-top: 5px;
  padding-bottom: 5px;
}
.timeline-column table tr .timeline-cell-name {
  width: 115px;
  max-width: 115px;
  padding-left: 4px;
  padding-right: 7px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: right;
}
.timeline-column table tr .timeline-cell-name a {
  color: #000;
}
.timeline-column table tr .timeline-cell-name.abbr {
  width: 59px;
  max-width: 59px;
}
.timeline-column table tr .timeline-cell-flag {
  width: 20px;
  max-width: 20px;
  padding: 0 0 2px 0;
}
.timeline-column table tr th.timeline-cell-score {
  text-align: center;
  display: none;
}
.timeline-column table tr td.timeline-cell-score {
  text-align: right;
  padding-right: 2px;
  display: none;
  width: 42px;
  max-width: 42px;
}
.timeline-column table tr th.timeline-cell-rank {
  text-align: center;
}
.timeline-column table tr td.timeline-cell-rank {
  text-align: right;
  padding-right: 8px;
  width: 36px;
  max-width: 36px;
}
.timeline-column table tr.hover td {
  background: #ccc;
}
