#report-generator {
  margin-bottom: 20px;
  position: relative;
  background-color: #fff;
  font-size: 15px;
}
#report-generator .accordion-group {
  border: none;
}
#report-generator .accordion-group .accordion-toggle {
  padding: 10px 15px;
}
#report-generator .subtitle {
  font-weight: bold;
}
#report-generator .customize-link {
  display: block;
  position: absolute;
  right: 20px;
  top: 8px;
}
#report-generator .nav {
  margin-bottom: 0;
}
#report-generator .inner {
  padding-bottom: 7px;
  padding-left: 7px;
  border: 1px solid #ccc;
  border-top: none;
  -webkit-border-bottom-right-radius: 5px;
  -webkit-border-bottom-left-radius: 5px;
  -moz-border-radius-bottomright: 5px;
  -moz-border-radius-bottomleft: 5px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}
#report-generator .less {
  font-size: 11px;
  line-height: 13px;
  padding-top: 10px;
}
#report-generator .less p {
  margin-bottom: 4px;
}
#report-generator .select-or-clear {
  margin-top: 8px;
  text-align: center;
}
#report-generator .group-toggler.active {
  background: #0088CC !important;
  color: #fff;
}
#report-generator #toggle-boxes {
  width: 345px;
  padding-top: 4px;
  padding-left: 37px;
}
#report-generator #toggle-boxes .toggle-box {
  background: #f0f0f0;
  color: #ccc;
  display: block;
  text-decoration: none;
  position: relative;
  border: 1px solid #ccc;
  margin-left: -1px;
  margin-top: -1px;
  float: left;
  width: 23px;
  height: 20px;
  text-align: center;
  padding-top: 2px;
  padding-bottom: 1px;
  font-size: 12px;
  letter-spacing: -1px;
}
#report-generator #toggle-boxes .toggle-box:hover,
#report-generator #toggle-boxes .toggle-box.hover {
  background: rgba(255, 122, 34, 0.4) !important;
  color: #888;
}
#report-generator #toggle-boxes .toggle-box.select {
  background: #fff;
  color: #000;
  border-color: #000;
  z-index: 1;
}
#report-generator .grouplist h6 {
  line-height: 15px;
  vertical-align: bottom;
  font-size: 12px;
  border-bottom: 1px solid #ccc;
  margin: 6px 0 4px 0;
  padding: 0 5px 2px 5px;
}
#report-generator .grouplist ul {
  margin-left: 3px;
  list-style-type: none;
}
#report-generator .grouplist ul li {
  line-height: 13px;
  font-size: 12px;
}
#report-generator .grouplist ul li a {
  display: block;
  padding: 1px 0 1px 4px;
  margin-bottom: 1px;
}
#report-generator .grouplist ul li a:hover {
  background: #ddd;
  text-decoration: none;
}
#report-generator .grouplist a:hover {
  text-decoration: underline;
}
#report-generator .grouplist.col4 {
  text-align: right;
}
#report-generator .grouplist.col4 a {
  padding: 2px 10px;
}
#report-generator .grouplist.col4 a.active {
  background: #0088CC;
  color: #fff;
}
#report-generator .grouplist.col4 .report-download {
  display: inline-block;
  margin-top: 8px;
  text-align: center;
  position: absolute;
  right: 15px;
  bottom: 15px;
}
#report-generator .grouplist.col4 .report-download .btn-download {
  padding: 3px 1px;
  height: 30px;
  font-size: 14px;
}
#report-generator .grouplist.col4 .report-download a.download-csv {
  color: #fff;
  padding: 5px 19px;
}
#report-generator .grouplist.col4 .report-download a.download-csv:hover {
  text-decoration: none;
}
#report-generator .col1 {
  float: left;
  width: 400px;
  margin-right: 10px;
}
#report-generator .col1 .col11,
#report-generator .col1 .col12 {
  float: left;
  margin-right: 10px;
}
#report-generator .col1 .col11 {
  width: 180px;
}
#report-generator .col1 .col12 {
  width: 200px;
}
#report-generator .col3 {
  float: left;
  width: 370px;
}
#report-generator .col4 {
  float: right;
  width: 176px;
}
