.document-availability h2 {
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
  font-size: 36px;
  margin: 0;
  padding: 5px 0 20px 10px;
}
.document-availability .col4 {
  float: right;
  width: 200px;
}
.document-availability .col4 #region {
  border: solid 1px #CCCCCC;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px;
  padding: 10px;
  margin-top: 15px;
}
.document-availability .col4 #region h6 {
  margin-top: 0px;
}
#av-region-toggler {
  margin: 0px;
  font-size: 12px;
}
#av-region-toggler li {
  display: block;
}
#av-region-toggler li a {
  padding: 2px 10px;
  display: block;
}
.av-region-toggler.active {
  background: none repeat scroll 0 0 #0088CC;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  border-radius: 3px;
  color: #FFFFFF;
}
#availability-key {
  width: 100%;
  font-size: 11px;
  line-height: 12px;
  border: 1px solid #999;
}
#availability-key th {
  background: #999;
  color: #fff;
  font-weight: bold;
  text-align: center;
  padding: 4px 0;
}
#availability-key td.dot {
  text-align: center;
  width: 22px;
}
#availability {
  width: 712px;
}
#availability td.dot,
#availability td.score {
  width: 52px;
}
#availability td.score {
  border-right: 1px solid #ccc;
}
#availability thead {
  height: 56px;
  display: block;
}
#availability thead tr {
  position: fixed;
  background: #ffffff;
}
#availability thead td {
  font-size: 12px;
  line-height: 12px;
  text-align: center;
  vertical-align: bottom;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ccc;
}
#availability thead td.country {
  width: 120px;
}
#availability thead td.flag {
  width: 31px;
  padding: 0 10px;
}
#availability thead td.dot {
  width: 48px;
}
#availability thead td.score {
  width: 47px;
}
#availability tbody td {
  text-align: center;
  vertical-align: middle;
}
#availability tbody td.country {
  font-size: 12px;
  text-align: right;
  width: 125px;
}
#availability tbody td.country a {
  color: #000;
}
#availability tbody td.flag {
  padding: 0 10px;
  vertical-align: middle;
  width: 30px;
}
