/* Bootstrap hack */
.badge-success {
  background: #54A954 !important;
}
.surveys-information {
  margin-bottom: 22px;
  margin-right: 10px;
  margin-left: 10px;
  font-size: 16px;
}
#datasheet-toggles .btn {
  width: 200px;
}
div.country-nav-select-outer {
  margin: 10px 0 0 10px;
}
div.country-nav-select-outer h4 {
  vertical-align: middle;
}
div.country-nav-select-outer h3 {
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
}
div#country-profile {
  margin: 0 10px;
  border-top: 1px solid #999;
}
div#country-profile .main-website-link {
  float: right;
  margin-top: 10px;
}
div#country-profile h2 {
  padding-bottom: 4px;
}
div#country-profile h3 {
  font-family: "Roboto Condensed", Arial, sans-serif;
  text-transform: uppercase;
}
div#country-profile .percentages .year {
  text-align: right;
}
div#country-profile .percentages .score {
  width: 90px;
  padding: 0 10px;
  text-align: right;
  font-weight: bold;
}
div#country-profile .percentages .bar {
  width: 800px;
}
div#country-profile .percentages .bar .percentbar {
  width: 100%;
  line-height: 10px;
}
div#country-profile .details {
  float: left;
}
div#country-profile .details table {
  margin-top: 0;
  width: 340px;
}
div#country-profile .details table tr th {
  text-align: center;
  border-bottom: 1px solid #ccc;
}
div#country-profile .details table tr th.space {
  background: #fff;
  border-color: transparent;
}
div#country-profile .details table tr * {
  border: none;
}
div#country-profile .details table tr td.number {
  width: 40px;
  background: #eee;
  padding-right: 10px;
  text-align: right;
  font-weight: bold;
}
div#country-profile .details table tr td.letter {
  text-align: center;
}
div#country-profile .details table tr td.letter.multi {
  background: #e9f4e9;
}
div#country-profile .details table tr td.letter.multi img {
  cursor: pointer;
}
div#country-profile .details table tr td.letter.multi img.inactive {
  opacity: 0.3;
}
div#country-profile .details table tr td.letter.multi img.inactive:hover {
  opacity: 0.7;
}
div#country-profile .details table tr td.letter.multi img.active {
  border: 1px solid #555;
  background: #fff;
}
div#country-profile .details table tr td.space {
  width: 8px;
}
div#country-profile .details table tr.hover td.number {
  background: #ddd;
}
div#country-profile .details table tr.hover td.space,
div#country-profile .details table tr.hover td.letter {
  background: #eee;
}
div#country-profile .details table tr.hover td.space.multi,
div#country-profile .details table tr.hover td.letter.multi {
  background: #dceddc;
}
div#country-profile .question-box {
  position: absolute;
  left: 300px;
  top: 0px;
  background: #eee;
  width: 540px;
  padding: 10px;
}
div#country-profile .question-box .question-text {
  background: #ddd;
  padding: 8px;
}
div#profile-mode {
  border: 1px solid #000;
  border-left: none;
  border-right: none;
  margin: 15px 0;
  padding: 10px 0;
  position: relative;
}
div#profile-mode .explanation {
  position: absolute;
  left: 178px;
  top: 12px;
  font-size: 13px;
  line-height: 18px;
  margin: 5px 0 0 0;
}
div#profile-mode #profile-toggle {
  float: right;
}
div#profile-mode .scores {
  font-weight: normal;
}
div#profile-mode .scores .year-box {
  padding: 8px;
  width: 60px;
  padding-bottom: 5px;
  float: left;
  border: 1px solid #999;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  text-align: center;
  margin-right: 6px;
}
div#profile-mode .scores .year-box span {
  display: block;
}
div#profile-mode .scores .year-box .top {
  font-size: 20px;
  padding-bottom: 4px;
  font-weight: normal;
}
div#profile-mode .scores .year-box .bottom {
  font-size: 12px;
}
